import React from "react";
import { Link } from "react-router-dom";

function HometaskMain() {
  return (
    <div className="GlobalButtonContainer">
      <div className="container">
        <div className="preview">
          <div className="Button">
            <Link className="btn btn-secondary" to="/hometask/OOP">OOP</Link>
          </div>
        </div>
        <div className="preview">
          <div className="Button">
            <Link className="btn btn-secondary" to="/hometask/SQ">SQ</Link>
          </div>
        </div>
      </div>
    </div>
  );
}

export default HometaskMain;