import React, { useState } from "react";
import { Link } from "react-router-dom";

function DropdownMenu() {
  const [isOpen, setIsOpen] = useState(false);

  const toggleMenu = () => setIsOpen(!isOpen);

  return (
    <div className="dropdown">
      <button className="dropdown-button" onClick={toggleMenu}>
        ≡
      </button>
      <div 
        className={`dropdown-menu ${isOpen ? 'open' : ''}`} 
        style={{
          transform: isOpen ? 'translateX(0)' : 'translateX(-100%)', 
          transition: 'transform 0.3s ease',
        }}
      >
        <button className="close-dropdown-button" onClick={toggleMenu}>←</button>
        <div className="dropdown-panel">
          <div className="preview">
            <div className="Button">
              <Link className="btn btn-secondary" to="/">Home</Link>
            </div>
          </div>
          <div className="preview">
            <div className="Button">
              <Link className="btn btn-secondary" to="/hometask">Hometask</Link>
            </div>
          </div>
          <div className="preview">
            <div className="Button">
              <Link className="btn btn-secondary" to="/game">Game</Link>
            </div>
          </div>
          <div className="preview">
            <div className="Button">
              <Link className="btn btn-secondary" to="/guide">Guide</Link>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default DropdownMenu;