import React from "react";
import { Link } from "react-router-dom";

function Home() {
  return (
    <div className="GlobalButtonContainer">
      <div className="ButtonPanel">
        <div className="container">
          <div className="preview">
            <div className="Button">
              <Link className="btn btn-secondary" to="/hometask">Hometask</Link>
            </div>
          </div>
          <div className="preview">
            <div className="Button">
              <Link className="btn btn-secondary" to="/game">Game</Link>
            </div>
          </div>
          <div className="preview">
            <div className="Button">
              <Link className="btn btn-secondary" to="/guide">Guide</Link>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Home;