import React, { useState, useEffect } from 'react';
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter';
import { vscDarkPlus } from 'react-syntax-highlighter/dist/esm/styles/prism';

const CodeDisplay = ({ url, language = 'javascript' }) => {
    const [codeString, setCodeString] = useState('');
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    useEffect(() => {
        const fetchData = async () => {
            try {
                const response = await fetch(url);
                if (!response.ok) {
                    throw new Error(`HTTP error! status: ${response.status}`);
                }
                const data = await response.text();
                setCodeString(data);
                setLoading(false);
            } catch (error) {
                setError(error.message);
                setLoading(false);
            }
        };

        fetchData();
    }, [url]);

    if (loading) {
        return <div>Loading...</div>;
    }

    if (error) {
        return <div>Error: {error}</div>;
    }

    return (
        <div style={{ width: '95%', padding: '10px', margin: '10px' }}>
            <SyntaxHighlighter
                language={language}
                style={vscDarkPlus}
                customStyle={{
                    width: '95%', // робимо ширину 100% відносно контейнера
                    whiteSpace: 'pre', // забезпечуємо, щоб код не переносився
                    wordWrap: 'normal', // не дозволяємо переносити слова
                }}
            >
                {codeString}
            </SyntaxHighlighter>
        </div>
    );
};

export default CodeDisplay;
