import React from "react";
import CodeDisplay from "../../components/CodeDisplay";

// Import images
import test1 from "./SQ/img/test1.png";
import test2and3 from "./SQ/img/test2and3.png";

function SQ() {
    return (
        <div className="GlobalButtonContainer">
            <div class="panel">
                <div id="section1" class="sections"></div>
                <div class="descriptionPanel" id="text1">
                    <h2>Unit тести</h2>
                    <h4>23.10.2024</h4>
                    <h2>Завдання</h2>
                    <p>Напишіть unit тести для 3-4 окремих функцій або методів.
                        Завдання полягає в тому, щоб вибрати один із ваших попередніх проєктів або фрагмент коду, який ви писали раніше, і написати до нього unit тести.</p>
                </div>
                <div class="descriptionPanel" id="text1">
                    <h2>Тести до робіт по ООП</h2>
                    <h2>Тест №1</h2>
                    <p>ця програма виводить інформацію про людей, які мають день народження в заданому місяці</p>
                    <h2>test1.js</h2>
                    <CodeDisplay url="/tasks/SQ/lab3/src/test1.js" language="javascript" />
                    <h2>test1.test.js</h2>
                    <CodeDisplay url="/tasks/SQ/lab3/__tests__/test1.test.js" language="javascript" />
                    <div class="imageContainer">
                        <img class="TaskImg" src={test1} alt="triangle"></img>
                    </div>
                </div>
                <div class="descriptionPanel" id="text1">
                    <h2>Тест №2</h2>
                    <p>масив товарів та інтерфейс в консолі</p>
                    <h2>test2.js</h2>
                    <CodeDisplay url="/tasks/SQ/lab3/src/test2.js" language="javascript" />
                    <h2>test2.test.js (інтеграційний тест)</h2>
                    <CodeDisplay url="/tasks/SQ/lab3/__tests__/test2.test.js" language="javascript" />
                    <h2>Тест №3</h2>
                    <h2>test3.test.js (end to end тест)</h2>
                    <CodeDisplay url="/tasks/SQ/lab3/__tests__/test3.test.js" language="javascript" />
                    <div class="imageContainer">
                        <img class="TaskImg" src={test2and3} alt="triangle"></img>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default SQ;